import React from 'react';
import './App.css';
import styled from "styled-components";
import Top from './Description/Top.js';
import Info from "./Description/Info";

const Container = styled.div`
    display: flex;
    flex-direction: column;
    width: 100% ;
    background:  #f8f5f2;
`;

const Main = styled.div`
    transition: 300ms;
    max-width: 1900px ;
    width: 100%;
    box-sizing: border-box;
    padding: 35px 30px;
    align-self: center;
    display: grid;
    min-height: 100vh;
    grid-template-columns: 2fr 10fr 100px 1fr ;
    grid-template-rows: auto;
    grid-gap: 10px;
    //overflow-x: hidden;
    @media (max-width: 1300px) {
      grid-template-columns: 100px 10fr 100px;
      grid-template-rows: auto;
    }
    @media (max-width: 970px) {
      grid-template-columns: 1fr;
      grid-template-rows: auto;
    }
    @media (max-width: 600px) {
      padding: 20px 30px;
    }
    @media (max-width: 410px) {
      padding: 10px 5px;
    }
    @media (max-width: 410px) {
      padding: 0;
    }
`;


const Decoration = styled.div`
    grid-area: 1/3/2/4;
    position: sticky;
    position: -webkit-sticky;
    top: 0;
    //background-color: #61dafb;
    width: 100px;
    height: 100px;
    display: grid;
    grid-template-columns: repeat(8,1fr);
    grid-template-rows: repeat(8,1fr);
    grid-gap: 2px;
    padding: 5px;
    animation: rotate 1s ease-in-out 1;
    :hover {
      animation: rotate 2s ease-in-out infinite alternate;
      .element-1 {
        animation: move-1 1s ease-in-out infinite alternate;
        transition: 0ms;
      }
      .element-2 {
        animation: move-2 1s ease-in-out  infinite alternate;
        transition: 0ms;
      }
      .element-3 {
        animation: move-3 1s ease-in-out  infinite alternate;
        transition: 0ms;
      }
      .element-4 {
        animation: move-4 1s ease-in-out infinite alternate;
        transition: 0ms;
      }
    }
    @media (max-width: 970px) {
      display: none;
    }
`;

const Description = styled.div`
    padding: 40px 30px;
    padding-bottom: 0;
    display: grid;
    background-color:#d1d1e9;
    grid-template-rows: 200px 1px 1fr;
    border-radius: 5px;
    border: 0;
    grid-row-gap: 28px;
    grid-area: 1/2/2/3;
    align-self: start;
    @media (max-width: 1000px) {
      grid-template-rows: 228px 1px 1fr;
    }
    @media (max-width: 970px) {
      grid-template-rows: 220px 1px 1fr;
    }
    @media (max-width: 940px) {
      grid-area: 1/1/2/2;
      grid-template-rows: 220px 1px 1fr;
    }
    @media (max-width: 710px) {
      grid-template-rows: 240px 1px 1fr;
    }
    @media (max-width: 650px) {
      grid-template-rows: 274px 1px 1fr;
    }
    @media (max-width: 600px) {
      grid-template-rows: 424px 1px 1fr;
    }
    @media (max-width: 540px) {
      grid-template-rows: 460px 1px 1fr;
    }
    @media (max-width: 455px) {
      grid-row-gap: 15px;
      grid-template-rows: 474px 1px 1fr;
    }
    @media (max-width: 360px) {
      grid-template-rows: 514px 1px 1fr;
      padding: 5px 10px;
    }
`;

const Line =styled.div`
  justify-self: center;
  height: 1px;
  width: 100%;
  background-color: white;
`;


function App() {
    return (
        <Container>
            <Main>
                <Decoration>
                    <div className='element element-1'/>
                    <div className='element element-2'/>
                    <div className='element element-3'/>
                    <div className='element element-4'/>
                </Decoration>
                <Description>
                    <Top/>
                    <Line/>
                    <Info/>
                </Description>

            </Main>
        </Container>
    );
}

export default App;
